import { useEffect, useState } from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { LocalDate, DateTimeFormatter } from '@js-joda/core'
import { Locale } from '@js-joda/locale_en-us'

import Table, { Column } from 'components/BTable'

import { useAdminPage, Context } from '../index'

import api from 'utils/api'

const columns: Column[] = [
  {
    label: 'First Name',
    attributeKey: 'firstName',
    sortable: true
  },
  {
    label: 'Last Name',
    attributeKey: 'lastName',
    sortable: true
  },
  {
    label: 'Matric Number',
    attributeKey: 'matricNumber',
    sortable: true
  },
  {
    label: 'Grade',
    attributeKey: 'grade',
    sortable: true
  },
  {
    key: 'hours',
    label: 'Time',
    attributeKey: 'minutesAttended',
    sortable: true,
    transform: (value: number) => {
      return `${Math.floor(value / 60)}h, ${value % 60}m`
    }
  },
  {
    key: 'total',
    label: 'Total Minutes',
    attributeKey: 'minutesAttended',
    sortable: true
  },
  /*{
    label: 'Latest Session',
    attributeKey: 'lastSessionDate',
    sortable: true,
    transform: value =>
      LocalDate.of(value.year, value.month, value.day).format(
        DateTimeFormatter.ofPattern('MMMM d').withLocale(Locale.ENGLISH)
      )
  },*/
  {
    label: '',
    attributeKey: 'studentSchoolYearGuid',
    sortable: false,
    transform: (value: string) => (
      <div className='d-flex justify-content-center'>
        <Link to={value} style={{ color: 'inherit' }}>
          View
        </Link>
      </div>
    )
  }
]

export default (): JSX.Element => {
  const { user }: Context = useAdminPage()
  const [state, setState] = useState<any[]>([])

  function getStudents (): void {
    api
      .get('attendance/records')
      .then(res => setState(res.data))
      .catch(err => console.warn(err))
  }

  useEffect(() => {
    getStudents()
  }, [])

  return (
    <Container>
      <Row className='my-3'>
        <Col>
          <h5>Students for {user.organizationName}</h5>
          <Table columns={columns} dataset={state} />
        </Col>
      </Row>
    </Container>
  )
}
