import React, { ReactNode } from 'react'
import { NavLinkProps } from 'react-router-dom'

import { Auth, IdentityClaim } from 'utils/authentication'

import { Container, Nav, Link as NavLink } from './styles'

interface NavigationProps {
  auth: Auth
  children: ReactNode
}

const RenderLinks = (props: NavigationProps): JSX.Element => (
  <>
    <ul data-testid='nav-list'>
      {React.Children.toArray(props.children).map((child, index) => {
        return (
          <li key={`nav-item-${index}`}>
            {child}
          </li>
        )
      })
      }
    </ul>
    <div>Authenticated as {props.auth.userName}</div>
  </>
)

export const Navigation = (props: NavigationProps): JSX.Element => {

  const filteredLinks = React.Children.toArray(props.children).filter((child) => {
    if (React.isValidElement(child) && typeof child.type !== 'string') {
      if (child.props.requiredType !== undefined && !props.auth.isAuthorized(child.props.requiredType)) {
        return false
      }
      return true
    }
  })

  return (
    <Container>
      <Nav linkCount={filteredLinks.length}>
        <RenderLinks auth={props.auth} children={filteredLinks} />
      </Nav>
    </Container>
  )
}


export const Link = (props: NavLinkProps): JSX.Element => (
  <NavLink
    requiredtype={IdentityClaim.Coordinator}
    {...props}
  >
    {props.children}
  </NavLink>
)

interface ProtectedLinkProps extends NavLinkProps {
  requiredType?: IdentityClaim
}

export const ProtectedLink = ({ to, requiredType, ...props }: ProtectedLinkProps): JSX.Element => (
  <NavLink
    to={to}
    requiredtype={requiredType}
    {...props}
  >
    {props.children}
  </NavLink>
)
