import { useState } from 'react'
import { Container, Button, Spinner } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import styled from 'styled-components'

import Search from './Search'
import Table, { Column } from 'components/BTable'
import { useAdminPage, Context } from '../index'
import { DropdownOption } from 'types/Session'

import paths from 'utils/routing/paths'

const Plus = styled.div`
  height: 1.2rem;
  width: 1.2rem;
  display: inline-block;

  line-height: 1rem;
  border: 1px solid var(--bs-gray-400);
  border-radius: 50%;
`

function dropdownOptionTransform (value: DropdownOption): string {
  return value.label
}

const columns: Column[] = [
  {
    label: 'Name',
    attributeKey: 'name',
    sortable: true
  },
  {
    label: 'Activity',
    attributeKey: 'activity',
    sortable: true,
    transform: dropdownOptionTransform
  },
  {
    label: 'Session Type',
    attributeKey: 'sessionType',
    sortable: true,
    transform: dropdownOptionTransform
  },
  {
    label: 'Schedule',
    attributeKey: 'recurring',
    sortable: true
    //transform: dropdownOptionTransform
  },
  {
    label: '',
    attributeKey: 'sessionGuid',
    sortable: false,
    transform: (value: string) => (
      <div className='d-flex justify-content-center'>
        <Link to={value} style={{ color: 'inherit' }}>
          View
        </Link>
      </div>
    )
  }
]

const TableView = ({ state }): JSX.Element => {
  return <Table columns={columns} dataset={state} />
}

//Just use the organization a user is tied to on the API side, no need to send it from here.
export default (): JSX.Element => {
  const { user }: Context = useAdminPage()
  const [state, setState] = useState([])
  const [isLoading, setIsLoading] = useState<boolean>(false)

  function handleChange (sessions): void {
    setState(sessions)
  }

  return (
    <>
      <Button
        variant='primary'
        as={Link}
        className='d-flex align-items-center mx-3 mt-3'
        to={`${paths.Edit.path}/${paths.Edit.Sessions.path}/overview`}
        style={{ width: 'fit-content' }}
      >
        Add New Session &nbsp;<Plus>+</Plus>
      </Button>

      <Search
        handleChange={handleChange}
        handleLoading={(isLoading: boolean) => setIsLoading(isLoading)}
      />

      <Container className='px-3'>
        <h5>Sessions for {user.organizationName}</h5>
        {isLoading ? (
          <Spinner animation='border' />
        ) : !state || state.length === 0 ? (
          <div className='d-flex align-items-center justify-content-center'>
            <p>No sessions found...</p>
          </div>
        ) : (
          <TableView state={state} />
        )}
      </Container>
    </>
  )
}
