import { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { Row, Col, Card } from 'react-bootstrap'

import { PageContainer } from 'styles'

import BasicDetails from './BasicDetails'
import AggregateDetails from './AggregateDetails'
import RegistrationDetails from './RegistrationDetails'
import AttendanceDetails from './AttendanceDetails'

import {
  Student,
  StudentSchoolYearWithRecordsView,
  StudentSchoolYearWithRecordsDomain
} from 'Models/Student'

import api from 'utils/api'

//info needed:
//basic student info
//Student aggregates

//registrations
//attendance history, default sorted by date
//

export default (): JSX.Element => {
  const { studentGuid } = useParams()
  const [state, setState] = useState<StudentSchoolYearWithRecordsView | null>()
  const [error, setError] = useState<string>('')

  useEffect(() => {
    //get student details
    api
      .get<StudentSchoolYearWithRecordsDomain>(`students/${studentGuid}`)
      .then(res => {
        setState(Student.toViewModel(res.data))
      })
      .catch()
  }, [])

  if (!studentGuid || !state) return <></>

  return (
    <PageContainer>
      <Card>
        <Card.Title></Card.Title>
        <Card.Body>
          <p>{error}</p>
          <Row lg={2}>
            <Col>
              <BasicDetails student={state} />
            </Col>
            <Col>
              <AggregateDetails minutes={state.minutesAttended} />
            </Col>
          </Row>
          <Row>
            <RegistrationDetails registrations={state.registrations} />
          </Row>
          <Row>
            <AttendanceDetails attendance={state.attendance} />
          </Row>
        </Card.Body>
      </Card>
    </PageContainer>
  )
}
