import { Guid } from 'guid-typescript'
import api from 'utils/api'

export enum IdentityClaim {
  Administrator = 0,
  Coordinator = 1,
  Unauthenticated = 2
}

export interface IUser {
  userName: string
  badgeNumber: string
  organizationGuid: Guid
  organizationName: string
  claim: IdentityClaim
}

export class Auth {
  public static initUser (): Promise<Auth> {
    return new Promise((resolve, reject) => {
      api
        .get('user')
        .then(res => {
          resolve(new Auth(res.data))
        })
        .catch(err => {
          console.warn('Could not fetch user data', err)
          //What should we do on an unsuccesful authentication??
          reject()
        })
    })
  }

  private readonly _user: IUser | null

  constructor (user: IUser | null) {
    this._user = user
  }

  public get userName (): string {
    if (this._user) return this._user.userName

    return 'Unauthenticated User'
  }

  public get badgeNumber (): string {
    if (this._user) return this._user.badgeNumber

    return 'Unauthenticated User'
  }

  public get organizationGuid (): Guid {
    if (this._user) return this._user.organizationGuid

    return Guid.createEmpty()
  }

  public get organizationName (): string {
    if (this._user) return this._user.organizationName

    return 'No Organization'
  }

  public get claim (): IdentityClaim {
    if (this._user) return this._user.claim

    return IdentityClaim.Unauthenticated
  }

  public isAuthorized (requiredClaim: IdentityClaim): boolean {
    if (this.claim <= requiredClaim) return true

    return false
  }
}
