import { useParams, Link, useNavigate } from 'react-router-dom'
import { useEffect, useState } from 'react'
import {
  ListGroup,
  Spinner,
  Card,
  Row,
  Col,
  Button,
  Modal
} from 'react-bootstrap'
import { DateTimeFormatter, LocalDate } from '@js-joda/core'
import { Locale } from '@js-joda/locale_en-us'

import { PageContainer } from 'styles'
import Alert, { ApiResult } from 'components/ApiResultAlert'
import ListItem, { Item } from 'components/Item'
import SearchStudentsModal from './SearchStudentsModal' //pull this out into a component rather than subcomponent of sessionDetails
import SessionAttendance from 'components/SessionAttendance'
import RegistrationsView from './RegistrationsView'

import { DayScheduleView } from 'Models/DaySchedule'
import { Session, SessionDomain, SessionView } from 'Models/Session'
import {
  StudentRegistration,
  StudentRegistrationDomain,
  StudentRegistrationView
} from 'Models/StudentRegistration'
import { StudentAttendanceDto, StudentRecord } from 'Models/StudentAttendance'

import api from 'utils/api'
import paths from 'utils/routing/paths'

//Nice to have - Calender visual view, but only something to *come back to*
export default ({}) => {
  const navigate = useNavigate()
  const { sessionGuid } = useParams()
  const [session, setSession] = useState<SessionView | null>(null)
  const [studentRegistrations, setStudentRegistrations] = useState<
    StudentRegistrationView[]
  >([])
  const [showStudentModal, setShowStudentModal] = useState<boolean>(false)
  const [attendanceApiResult, setAttendanceApiResult] = useState<
    ApiResult | undefined
  >(undefined)
  const [attendanceModalParams, setAttendanceModalParams] = useState({
    show: false,
    schedule: null
  })
  const [isLoading, setIsLoading] = useState<boolean>(false)

  /// /Temp stuff
  const [showSessionDeleteModal, setShowDeleteModal] = useState<boolean>(false)

  function handleSessionDeletion (deleteSession: boolean): void {
    if (deleteSession) {
      api
        .delete(`session/${sessionGuid}`)
        .then(() => navigate('/home/admin/sessions'))
        .catch()
    }
    setShowDeleteModal(false)
  }
  /// /

  /// /Functions
  function handleAttendanceModalClose (): void {
    setAttendanceModalParams({
      ...attendanceModalParams,
      show: false,
      schedule: null
    })
    getStudentRegistrations()
  }

  function removeStudentRegistrations (
    scheduleGuids: string[],
    studentSchoolYearGuid: string
  ) {
    api
      .delete('session/registration', {
        params: {
          studentSchoolYearGuid,
          dayScheduleGuid: scheduleGuids
        }
      })
      .then(res => /*dosomething*/ {})
      .catch()
      .finally(() => getStudentRegistrations())
  }

  function getStudentRegistrations (): void {
    api
      .get<StudentRegistrationDomain[]>(`session/${sessionGuid}/students`)
      .then(res => {
        //sorted for ease of display
        const registrations: StudentRegistrationView[] = res.data.map(item =>
          StudentRegistration.toViewModel(item)
        )
        setStudentRegistrations(registrations)
      })
      .catch(err => {
        console.warn(err)
      })
  }

  function submitAttendance (
    date: LocalDate,
    studentRecords: StudentRecord[],
    instructorRecords: any[]
  ): Promise<void> {
    return new Promise((resolve, reject) => {
      const studentRecordsParam = studentRecords
        .filter(record => record.isPresent !== false)
        .map(record => ({
          studentSchoolYearGuid: record.student.studentSchoolYearGuid,
          attendance: record.attendance
        }))

      const instructorRecordsParam = instructorRecords
        .filter(record => record.isPresent !== false)
        .map(record => ({
          instructorSchoolYearGuid: record.instructor.instructorSchoolYearGuid,
          attendance: record.attendance
        }))

      const params = {
        sessionGuid,
        date,
        studentRecords: studentRecordsParam,
        instructorRecords: instructorRecordsParam
      }

      api
        .post<StudentAttendanceDto>('attendance', params)
        .then(res => {
          handleAttendanceModalClose()
          setAttendanceApiResult({
            label: `Attendance for ${date}`,
            success: true,
            reason: []
          })
          resolve()
        })
        .catch(err => {
          handleAttendanceModalClose()
          setAttendanceApiResult({
            label: 'Attendance',
            success: false,
            reason: [
              'Error in recording attendance, contact ethan.shook2@tusd1.org if issues persist.'
            ]
          })
          reject()
        })
    })
  }

  function getSessionDetails (): void {
    setIsLoading(true)
    api
      .get<SessionDomain>(`session/${sessionGuid}`)
      .then(res => {
        const session: SessionView = Session.toViewModel(res.data)
        setSession(session)
        document.title = `View Session - ${session.name}`
      })
      .catch(err => console.warn(err))
      .finally(() => setIsLoading(false))
  }

  /// /Effects

  useEffect(() => {
    getSessionDetails()
    getStudentRegistrations()
  }, [sessionGuid])

  useEffect(() => {
    if (!showStudentModal) {
      getStudentRegistrations()
    }
  }, [showStudentModal])

  if (!session && isLoading) return <Spinner animation='border' role='status' />
  else if (!session && !isLoading)
    return <p>An error occured while loading the session.</p>

  /// /JSX

  return (
    <PageContainer>
      <Card>
        <Card.Title>
          <Alert apiResult={attendanceApiResult} />
        </Card.Title>
        <Card.Body>
          <Card.Title
            as={'h2'}
            className='d-flex align-items-center justify-content-center'
          >
            {session!.name}&nbsp;
            <Button
              variant='primary'
              //@ts-ignore
              as={Link}
              className='px-2 py-1'
              to={`${paths.Edit.path}/${paths.Edit.Sessions.path}/${sessionGuid}`}
              style={{ width: 'auto' }}
            >
              Edit
            </Button>
          </Card.Title>
          <Row>
            <Col className='w-50'>
              <Card>
                <Card.Body>
                  <Card.Title>Overview</Card.Title>
                  <ListGroup variant='flush'>
                    <ListItem
                      label='Session Type:'
                      value={session!.sessionType.label}
                    />
                    <ListItem
                      label='Activity:'
                      value={session!.activity.label}
                    />
                    <ListItem
                      label='Objective:'
                      value={session!.objective.label}
                    />
                    <ListItem
                      label='Funding Source:'
                      value={session!.fundingSource.label}
                    />
                    <ListItem
                      label='Organization Type:'
                      value={session!.organizationType.label}
                    />
                    <ListItem
                      label='Partnership Type:'
                      value={session!.partnershipType.label}
                    />
                    <ListItem
                      label='Grade Levels:'
                      value={
                        <p>
                          {session!.grades?.length
                            ? session!.grades.map((grade, index) =>
                                index !== session!.grades.length - 1
                                  ? grade.value + ', '
                                  : grade.value
                              )
                            : 'All Grade Levels'}
                        </p>
                      }
                    />
                  </ListGroup>
                </Card.Body>
              </Card>
            </Col>
            <Col className='w-50'>
              <Card>
                <Card.Body>
                  {session!.recurring ? (
                    <>
                      <Card.Title>Weekly Schedule</Card.Title>
                      <ListGroup variant='flush'>
                        {session!.daySchedules.map((item: DayScheduleView) => (
                          <Item>
                            <p>{item.dayOfWeek}</p>
                            <div className='d-flex flex-column'>
                              {item.timeSchedules.map(schedule => (
                                <p>
                                  {`${schedule.startTime.format(
                                    DateTimeFormatter.ofPattern(
                                      'hh:mm a'
                                    ).withLocale(Locale.ENGLISH)
                                  )} to ${schedule.endTime.format(
                                    DateTimeFormatter.ofPattern(
                                      'hh:mm a'
                                    ).withLocale(Locale.ENGLISH)
                                  )}`}
                                </p>
                              ))}
                            </div>
                            <Button
                              size='sm'
                              style={{ height: 'fit-content' }}
                              onClick={() => {
                                setAttendanceModalParams({
                                  show: true,
                                  schedule: item
                                })
                              }}
                            >
                              Attendance
                            </Button>
                          </Item>
                        ))}
                      </ListGroup>
                    </>
                  ) : (
                    <>
                      <Card.Title>Schedule</Card.Title>
                      <Card.Body>
                        <ListGroup variant='flush'>
                          <ListItem
                            label='Session Date:'
                            value={session!.firstSession.format(
                              DateTimeFormatter.ofPattern(
                                'MMMM, dd'
                              ).withLocale(Locale.ENGLISH)
                            )}
                          />
                          <ListItem
                            label={`Time${
                              session!.daySchedules[0]?.timeSchedules.length > 1
                                ? 's'
                                : ''
                            } of day:`}
                            value={
                              <p>
                                {session!.daySchedules[0]?.timeSchedules.map(
                                  timeSchedule => (
                                    <div>{`${timeSchedule.startTime.format(
                                      DateTimeFormatter.ofPattern(
                                        'hh:mm a'
                                      ).withLocale(Locale.ENGLISH)
                                    )} 
                                to ${timeSchedule.endTime.format(
                                  DateTimeFormatter.ofPattern(
                                    'hh:mm a'
                                  ).withLocale(Locale.ENGLISH)
                                )}`}</div>
                                  )
                                )}
                              </p>
                            }
                          />
                        </ListGroup>
                      </Card.Body>
                    </>
                  )}
                </Card.Body>
              </Card>
              <Card className='mt-3'>
                <Card.Body>
                  <Card.Title>Instructors</Card.Title>
                  <ListGroup
                    variant='flush'
                    className='d-flex flex-row flex-wrap'
                  >
                    {session!.instructors.map(instructor => (
                      <div className='w-50'>
                        <ListGroup.Item>{`${instructor.firstName} ${instructor.lastName}`}</ListGroup.Item>
                      </div>
                    ))}
                  </ListGroup>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row className='pt-3'>
            <Col>
              <Card>
                <Card.Body>
                  <Card.Title>
                    Students&nbsp;
                    <Button
                      variant='primary'
                      className='px-2 py-1'
                      style={{ width: 'auto' }}
                      onClick={() => {
                        setShowStudentModal(true)
                      }}
                    >
                      Add
                    </Button>
                  </Card.Title>
                  <Card.Text>
                    <RegistrationsView
                      registrations={studentRegistrations}
                      options={{}}
                      sessionGuid={sessionGuid}
                      onChange={removeStudentRegistrations}
                    />
                  </Card.Text>
                </Card.Body>
              </Card>
            </Col>
          </Row>
          <Row className='pt-5 d-flex flex-row justify-content-center'>
            <Button
              variant='danger'
              style={{ width: 'fit-content' }}
              onClick={() => setShowDeleteModal(true)}
            >
              Delete Session
            </Button>
          </Row>
        </Card.Body>
      </Card>
      <RemoveSessionModal
        sessionGuid={sessionGuid}
        session={session}
        show={showSessionDeleteModal}
        handleClose={handleSessionDeletion}
      />
      <SearchStudentsModal
        show={showStudentModal}
        handleClose={() => setShowStudentModal(false)}
        handleChange={() => null}
        scheduling={session!.daySchedules}
      />
      {attendanceModalParams.show ? (
        <SessionAttendance
          registrations={{
            instructors: session?.instructors,
            students: studentRegistrations
          }}
          daySchedule={attendanceModalParams.schedule}
          handleClose={handleAttendanceModalClose}
          handleSubmit={submitAttendance}
        />
      ) : null}
    </PageContainer>
  )
}

/*
 
          */

const RemoveSessionModal = ({
  sessionGuid,
  session,
  show,
  handleClose
}): JSX.Element => {
  const [deletionAllowed, setStatus] = useState<boolean>(false)

  useEffect(() => {
    api
      .get(`session/${sessionGuid}/status`)
      .then(res => {
        setStatus(res.data || false)
      })
      .catch()
  }, [])

  const message = deletionAllowed
    ? 'Are you sure you want to delete this session from your organization?'
    : 'Sorry, this session is not able to be deleted as attendance records already exist for it. In the future, you will be able to hide unwanted and unremoveable sessions.'

  const button = deletionAllowed ? (
    <Button onClick={() => handleClose(true)}>Delete Session</Button>
  ) : null

  return (
    <Modal show={show}>
      <Modal.Header closeButton onHide={() => handleClose(false)}>
        Deletion of {session.name}.
      </Modal.Header>
      <Modal.Body>{message}</Modal.Body>
      <Modal.Footer>{button}</Modal.Footer>
    </Modal>
  )
}
