import { ListGroup, Spinner } from 'react-bootstrap'

import ListItem from 'components/Item'

import { Grade } from 'Models/Grade'
import { StudentSchoolYearWithRecordsView } from 'Models/Student'

interface Props {
  student: StudentSchoolYearWithRecordsView
}

export default ({ student }: Props): JSX.Element => {
  if (!student) return <Spinner animation='border' role='status' />

  return (
    <ListGroup variant='flush'>
      <ListItem label='First Name:' value={student.firstName} />
      <ListItem label='Last Name:' value={student.lastName} />
      <ListItem label='Grade:' value={Grade.toOrdinalString(student.grade)} />
      <ListItem label='Matric Number:' value={student.matricNumber} />
    </ListGroup>
  )
}
