import { DaySchedule, DayScheduleDomain, DayScheduleView } from './DaySchedule'
import { Student, StudentSchoolYearView } from './Student'

export interface StudentRegistrationDomain extends StudentSchoolYearView {
  sessionGuid: string
  studentGuid: string
  sessionName: string
  firstName: string
  lastName: string
  matricNumber: string
  daySchedule: DayScheduleDomain
}

export interface StudentRegistrationView extends StudentSchoolYearView {
  sessionGuid: string
  sessionName: string
  daySchedule: DayScheduleView
}

export abstract class StudentRegistration {
  public static toViewModel (
    obj: StudentRegistrationDomain
  ): StudentRegistrationView {
    return {
      ...obj,
      daySchedule: DaySchedule.toViewModel(obj.daySchedule)
    }
  }
}
